<template>
    <textarea class="lm-textarea" rows="1"
              v-model="content"
              style="resize: vertical; font-size: 16px; min-height: 34px;line-height: normal; padding: 2px 6px 0 6px;"
              :style="{maxHeight:computedMaxHeight}"
    >
    </textarea>
</template>

<script>
    import '../common/scss/common.scss';
    import autosize from './autoresize';

    let Textarea = {
        props: {
            /**
             * initial content we should set to the textarea
             */
            initialContent: {
                type: [String],
                default: '',
            },

            /**
             * Max height for this text area
             */
            maxHeight: {
                type: [String, Number],
                default: '130px'
            }
        },
        computed: {
            computedMaxHeight: function () {
                return Number.isInteger(Number(this.maxHeight)) ? (this.maxHeight + 'px') : this.maxHeight;
            }
        },
        data: function () {
            return {
                content: this.initialContent
            };
        },
        'mounted': function () {
            const me = this;
            this.$nextTick(function () {
                autosize(me.$el);
            });
        },
        'beforeDestroy': function () {
            autosize.destroy(this.$el);
        }
    };

    export default Textarea;

</script>



